.Joke {
  display: flex;
  align-items: center;
  justify-content: center;
  border-bottom: 1px solid rgba(0, 0, 0, 0.4);
}

.Joke-rate {
  flex: 1 0 15%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.Joke-rate > * {
  margin: 0 0.5rem;
}

.Joke-rate-button {
  width: 15%;
  max-width: 2rem;
  cursor: pointer;
}

.Joke-rate-text {
  border-radius: 50%;
  min-width: 5.2rem;
  text-align: center;
  padding: 1.5rem;
  box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.4);
}

.rate-neg-15 {
  border: 2px solid #f44336;
}
.rate-neg-4 {
  border: 2px solid #ff5721;
}
.rate-0 {
  border: 2px solid #9f9f9f;
}
.rate-4 {
  border: 2px solid #ff9800;
}
.rate-6 {
  border: 2px solid #ffeb3b;
}
.rate-10 {
  border: 2px solid #8cc34a;
}
.rate-12 {
  border: 2px solid #4daf50;
}

.Joke-text {
  flex: 1 0 75%;
  color: rgb(31, 54, 117);
  text-align: center;
}

.Joke-emoji {
  flex: 1 0 10%;
  padding: 1.5rem;
}

.Joke-emoji img {
  width: 100%;
  box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.4);
  border-radius: 50%;
}

.loadingSpin {
  animation: spin 0.5s ease infinite;
}

@keyframes spin {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(360deg);
  }
}

@media only screen and (max-width: 900px) {
  .Joke {
    padding: 2rem;
  }

  .Joke-rate {
    flex: 1 0 20%;
  }
}

@media only screen and (max-width: 600px) {
  .Joke-emoji {
    display: none;
  }

  .Joke-rate {
    flex: 1 0 25%;
    flex-direction: column;
  }

  .Joke-rate > * {
    margin: 1rem;
  }

  .Joke-rate-button {
    width: 20%;
  }
}
