.Sidebar {
  flex: 1 0 30%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background: #9575cc;
  box-shadow: 0 1rem 3rem rgba(0, 0, 0, 0.4);
  position: relative;
  z-index: 2;
  border-radius: 3px;
}

.Sidebar > * {
  margin: 2rem 0;
}

.Sidebar-title {
  color: #fff;
  display: block;
  font-weight: bold;
  font-size: 4.4rem;
}

.Sidebar-title span {
  font-weight: lighter;
  font-family: Georgia, 'Times New Roman', Times, serif;
}

.Sidebar-img-container {
  width: 50%;
  border-radius: 50%;
  box-shadow: 0 1rem 3rem rgba(0, 0, 0, 0.4);
}

.Sidebar-img {
  width: 100%;
  display: block;
  border-radius: 50%;
  margin: 0 auto;
}

.Sidebar-button {
  border: none;
  background: linear-gradient(
    140deg,
    var(--color-pink) 0%,
    var(--color-pink) 50%,
    #2191d1 50%
  );
  display: block;
  padding: 1rem 2rem;
  border-radius: 5px;
  color: #fff;
  cursor: pointer;
  font-family: sans-serif;
  font-size: 1.8rem;
  position: relative;
  z-index: 1;
  box-shadow: 0 1rem 1.5rem rgba(0, 0, 0, 0.4);
  transition: box-shadow 0.3s ease;
}

.Sidebar-button::before {
  z-index: -1;
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(
    140deg,
    #2191d1 0%,
    #2191d1 50%,
    var(--color-pink) 50%
  );
  opacity: 0;
  transition: opacity 0.3s ease-in-out;
}

.Sidebar-button:hover::before {
  opacity: 1;
}

.Sidebar-button:active {
  box-shadow: none;
}

.Sidebar-button:focus {
  outline: none;
}

@media only screen and (max-width: 900px) {
  .Sidebar {
    flex: 1 0 100%;
  }

  .Sidebar-img-container {
    width: 25%;
  }
}

@media only screen and (max-width: 600px) {
  .Sidebar {
    box-shadow: none;
  }
}
