html {
  box-sizing: border-box;
  font-size: 62.5%;
  --color-blue: #b2e3fa;
  --color-pink: #ee6191;
}

*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

body {
  font-family: Arial, 'Helvetica Neue', Helvetica, sans-serif;
  font-size: 1.6rem;
  background: linear-gradient(
      140deg,
      var(--color-blue) 0%,
      var(--color-blue) 50%,
      var(--color-pink) 50%
    )
    no-repeat fixed;

  min-height: 100vh;
  overflow-x: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
}
